/* Media Queries */
@media (max-width: 768px) {
  .login-title {
    font-size: 24px; /* Ajusta el tamaño de fuente del título */
  }

  .form-group input {
    padding: 10px; /* Reduce el padding en los inputs */
  }

  .login-button,
  .register-button {
    padding: 10px; /* Ajusta el padding de los botones */
  }
}

@media (max-width: 480px) {
  .login-title {
    font-size: 20px; /* Ajusta aún más el tamaño del título */
  }

  .login-subtitle {
    font-size: 14px; /* Ajusta el tamaño del subtítulo */
  }

  .form-group input {
    padding: 8px; /* Reduce más el padding en inputs */
  }

  .login-button,
  .register-button {
    padding: 8px; /* Reduce más el padding de los botones */
  }
}
